.GamedetailsTitle {
	width: 100%;
	box-sizing: border-box;
	background-color: #64beb8;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 3rem 3rem;
}
.GamedetailsTitle p {
	font-style: normal;
	font-weight: normal;
	font-size: 28px;
	line-height: 34px;
	color: var(--primary-color);
}
.GamedetailsTitle h3 {
	font-style: normal;
	font-weight: bold;
	font-size: 48px;
	line-height: 59px;
	color: var(--primary-color);
}
