@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
	font-family: 'Montserrat', sans-serif;
}
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.mode-style {
	min-height: 100vh;
	background-color: var(--background-color);
}

.gamelist-card
{
    padding: 1rem;
    margin-bottom: 2rem;
    background-color: var(--primary-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 15rem;
    box-shadow: 2px 4px 8px rgba(252, 248, 217, 0.1);
    border-radius: 10px;
}
.gamelist-card .image
{
    width: 100%;
    margin-bottom: 1rem;
    border: 1px solid #EDE3E3;
    border-radius: 10px;
}
.gamelist-card .body {
    width: 100%;
}
.gamelist-card .body .title
{
    margin-bottom: 1rem;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--secondary-color);
}
.gamelist-card .body .description
{
    margin-bottom: 1rem;
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    color: var(--secondary-color);
}
.gamelist-card .body .played
{
    margin-bottom: 1rem;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: var(--secondary-color);
}
.gamelist-card .body .detail-wrap .detail
{
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #E53045;
}
.gamelist-card .body .detail-wrap
{
    width: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-decoration: none;
}
.member-navbar {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	background-color: #0e565d;
	padding: 1rem 0;
	position: fixed;
	width: 100%;
	z-index: 10;
}
.member-navbar .right {
	margin-right: 3rem;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.member-navbar .right .toggle-mode {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
}
.member-navbar .left {
	height: 22px;
	display: flex;
	flex-direction: row;
	margin-left: 3rem;
	align-items: center;
}
.member-navbar .center {
	height: 22px;
	display: flex;
	flex-direction: row;
	margin-left: 3rem;
	align-items: center;
}
.member-navbar .left .item.score {
	font-style: normal;
	font-weight: 300;
	font-size: 14px;
	line-height: 17px;
}
.member-navbar .right .item,
.member-navbar .left .item,
.member-navbar .center .item {
	text-decoration: none;
	color: #fcf8d9;
	margin-right: 1rem;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
}
.member-navbar .right .item {
	margin: 0 0.5rem;
}
.member-navbar .center .item {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
}
.member-navbar .item.smaller {
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
}
.member-navbar .item.active {
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
}

.header {
    padding-top: 5rem;
}
.header h1
{
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 39px;
    color: var(--primary-color);
}
section
{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search
{
    margin-top: 3rem;
}
.search .wrap
{
    border: 1px solid #C4C4C4;
    display: flex;
    align-items: center;
    padding: 0 1rem;
    width: 50%;
    background-color: white;
    border-radius: 10px;
}
.search .wrap .input
{
    width: 100%;
    padding: 1rem;
    border: none;
    outline: none;
}
.search .wrap .input:-ms-input-placeholder
{
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #C4C4C4;
}
.search .wrap .input::placeholder
{
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #C4C4C4;
}
section {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.section-game {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	padding: 3rem 5rem 0 5rem;
}

.button
{
    background: #E53045;
    border-radius: 8px;
    padding: 1rem 2rem;
    text-decoration: none;
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
}
.button:hover
{
    background-color: transparent;
    color: #E53045;
    border: 1px #E53045 solid;
}
.playerlist-card {
	width: 30rem;
	padding: 1rem;
	margin-bottom: 2rem;
	background: var(--primary-color);
	border-radius: 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.playerlist-card .image {
	height: 100%;
	border: 1px solid #ede3e3;
	border-radius: 10px;
}
.playerlist-card .body {
	height: 100%;
	padding-right: 5rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.playerlist-card .body h3 {
	margin-bottom: 1rem;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	color: var(--secondary-color);
}
.playerlist-card .body p {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 20px;
	color: var(--secondary-color);
}

.section-player {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: wrap;
	padding: 3rem 5rem 0 5rem;
}

.player-profile-wrap
{
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
}
.player-profile-wrap .image
{
    height: 10rem;
}
.player-profile-wrap .body
{
    padding-right: 10rem;
}
.player-profile-wrap .body .name
{
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
    color: var(--tertiary-color);
}
.player-profile-wrap .body .score
{
    font-style: normal;
    font-weight: 500;
    font-size: 48px;
    line-height: 59px;
    color: var(--tertiary-color);
}
.player-profile-wrap .body .email
{
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 39px;
    color: var(--tertiary-color);
}
.profile {
	width: 100%;
	margin-top: 5rem;
}

.back-button {
	display: flex;
	flex-direction: row;
	width: 100%;
	justify-content: flex-start;
	margin-bottom: 1rem;
    text-decoration: none;
}
.back-button p {
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 17px;
	color: var(--primary-color);
	margin-left: 1rem;
}
.GamedetailsTitle {
	width: 100%;
	box-sizing: border-box;
	background-color: #64beb8;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 3rem 3rem;
}
.GamedetailsTitle p {
	font-style: normal;
	font-weight: normal;
	font-size: 28px;
	line-height: 34px;
	color: var(--primary-color);
}
.GamedetailsTitle h3 {
	font-style: normal;
	font-weight: bold;
	font-size: 48px;
	line-height: 59px;
	color: var(--primary-color);
}

.play-button .item
{
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 34px;
    color: var(--primary-color);
    margin-right: 2rem;
}
.play-button
{
    background-color: #E53045;
    border-radius: 10px;
    width: 48%;
    padding: 2rem 3rem;
    box-sizing: border-box;
    display: flex;
    text-decoration: none;
    flex-direction: row;
    justify-content: center;
}
.play-button:hover
{
    background-color: transparent;
    color: #E53045;
    border: 1px #E53045 solid;
}
.GamedetailsStatus {
	background-color: #c4c4c4;
	border-radius: 10px;
	width: 48%;
	padding: 2rem 3rem;
	box-sizing: border-box;
	display: flex;
	text-decoration: none;
	flex-direction: row;
	justify-content: center;
}
.GamedetailsStatus .item {
	font-style: normal;
	font-weight: 500;
	font-size: 28px;
	line-height: 34px;
	color: var(--primary-color);
}
.GamedetailsStatus.played {
	background-color: #0e565d;
}

.GamedetailsPlayerListHeader {
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	color: var(--primary-color);
	background-color: #0e565d;
	border-radius: 10px 10px 0px 0px;
	box-sizing: border-box;
	padding: 2rem 1rem;
}
.playerlist {
	width: 30%;
}
.playerlist .list-player {
	padding: 0.5rem 0;
	display: flex;
	flex-direction: column;
	background-color: var(--primary-color);
	border-radius: 0 0 10px 10px;
}

.GamedetailsPlayerListItem {
	padding: 1rem 2rem;
	border-bottom: 0.5px #c4c4c4 solid;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.GamedetailsPlayerListItem p {
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 20px;
	color: var(--secondary-color);
}
.GamedetailsPlayerListItem:last-child {
	border-bottom: none;
}

pre {
	white-space: pre-wrap; /* css-3 */
	white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
	white-space: -pre-wrap; /* Opera 4-6 */
	white-space: -o-pre-wrap; /* Opera 7 */
	word-wrap: break-word; /* Internet Explorer 5.5+ */
}
.GamedetailsDescription {
	width: 65%;
}
.GamedetailsDescription .header {
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	color: var(--primary-color);
	background-color: #0e565d;
	border-radius: 10px 10px 0px 0px;
	box-sizing: border-box;
	padding: 2rem 1rem;
}
.GamedetailsDescription .description-inner {
	background: var(--primary-color);
	border-radius: 0 0 10px 10px;
	padding: 1rem;
	font-style: normal;
	font-weight: 300;
	font-size: 16px;
	line-height: 25px;
	color: var(--secondary-color);
}

.Gamedetails-detail
{
    padding: 3rem 10rem 0 10rem;
}
.Gamedetails-detail .list
{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 2rem;
    width: 100%;
}
.gamedetails-button
{
    width: 100%;
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.navbar {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background-color: #0e565d;
	padding: 1rem 0;
	position: fixed;
	width: 100%;
	z-index: 10;
}

.navbar .right {
	margin-right: 3rem;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.navbar .right .toggle-mode {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
}
.navbar .left {
	margin-left: 3rem;
}
.navbar .right .item,
.navbar .left .item {
	text-decoration: none;
	color: #fcf8d9;
}
.navbar .right .item {
	margin: 0 0.5rem;
}
.navbar .item.active {
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	color: #fcf8d9;
}

.auth-button
{
    background: #E53045;
    border-radius: 8px;
    padding: 0.5rem 2rem;
    text-decoration: none;
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    border: none;
}
.auth-button:hover
{
    background-color: transparent;
    color: #E53045;
    border: 1px #E53045 solid;
}
.login-modal
{
    margin-right: -10rem;
    background-color: var(--background-color);
    border-radius: 10px;
    padding: 3rem 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 15rem;
}
.login-modal h3
{
    margin-bottom: 1rem;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 39px;
    color: var(--primary-color);
}
.login-modal .form
{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.login-modal .form .text-input
{
    width: 100%;
    padding: 0.5rem 1rem;
    border: none;
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 10px;
    margin-bottom: 1rem;
}
.login-background {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}
.login-background h1 {
	width: 18rem;
	margin-right: 5rem;
	font-style: normal;
	font-weight: bold;
	font-size: 56px;
	line-height: 68px;
	color: var(--primary-color);
}

.register-modal
{
    margin-right: -10rem;
    background-color: var(--background-color);
    border-radius: 10px;
    padding: 3rem 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 15rem;
}
.register-modal h3
{
    margin-bottom: 1rem;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 39px;
